import React from 'react';
import { Panel,  Div, platform, HeaderButton, IOS, PanelHeader, Group} from '@vkontakte/vkui'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Text from './Text';
import './Persik.css'

let items = Array.from({ length: 50 })
const osname = platform()

const Birthday = props => (
	<Panel id={props.id}>
	<PanelHeader left={<HeaderButton onClick={() => props.back()}>
			{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
		</HeaderButton>}>
					 День рождения
					</PanelHeader>
					<img src="https://png.pngtree.com/thumb_back/fw800/background/20190418/pngtree-happy-birthday-background-image_104902.jpg" width="100%" height="150" ></img>
					<Group>
					<Div className='center'>
				Здесь ты можешь найти поздравления ко Дню рождения!
						</Div>
						</Group>
											{items.map((i, index) => (
										<Text  {...props} key={index} id={index} name='birthday'/>
									 		))}
											{ props.snackbar }
					</Panel>

);


export default Birthday;
