  'Поздравляю с Пасхой. Желаю верить и надеяться, мечтать и жить с любовью в сердце, ценить свою жизнь и красиво распоряжаться каждым её днём. Пусть Господь отводит от тебя любую беду, пусть этот праздник принесёт благодать и счастье.',
