  'Христос Воскрес! И пусть в твоей жизни произойдёт такое же великое чудо. Поздравляю с Пасхой и от души желаю надеяться, мечтать, творить добро, верить во всё хорошее, дарить окружающим любовь и получать в ответ искренность. Желаю мира и счастья в этом мире.',
  'Поздравляю с Пасхой и от души хочу пожелать светлой дороги к счастью и благу, великой радости для сердца и только добрых дней в жизни. Пусть Господь тебя хранит, пусть ангел никогда не даст тебе упасть, пусть любовь, надежда и вера помогают преодолевать все трудности и творить добро!',
  'Христос Воскрес! Поздравляю с праздником Пасхи и от души желаю хранить в своём сердце огонёк любви, надежды и веры, огонёк, который всегда будет вести тебя к светлому счастью. Желаю всех благ твоей семье, здоровья, мира и достатка, желаю тебе искренней радости и таких же великих чудес в жизни, как этот праздник.',
  'С праздником Воскресения Христа! Это самый светлый и радостный праздник у православных христиан, символ победы над смертью, торжество жизни. Пусть куличи, пасхи, раскрашенные яйца наполнят этот день светом, надеждой, радостью общения с родными, близкими и друзьями. Счастья и долголетия!',
  'Христос Воскрес! С добрым праздником Пасхи. Пусть в жизнь твою постучится светлое счастье, пусть для души этот день откроет врата радости. Желаю всего доброго и хорошего, желаю светлых мыслей и великодушных поступков, желаю истинной любви и Божьего благословения на благополучную долгую жизнь.',
  'Христос Воскрес. Со светлым праздником Пасхи. Желаю, чтобы в твоей жизни всегда горел свет блага, счастья, радости, надежды и удачи, желаю, чтобы Бог не оставлял тебя в беде, желаю, чтобы каждый день в твой дом и в твоё сердце приносил только добро, искренность, вдохновение, достаток и мир.',
