  'С великим праздником! Погрузитесь в океан светлых мыслей и интересного общения. Не торопитесь жить, наслаждайтесь каждым моментом, долгожданным событием, семейным отпуском. Пусть на пути вас ждет успешный результат любого сотрудничества. Стремитесь делать добро, жить по совести, искренне любить, прощать обиды и нелепые слова, больше читать и видеть в людях только хорошее.',
  'В светлый праздник Пасхи хочу пожелать, чтобы ваш дом был под защитой Господа. Пускай в нём будут тепло и уют, звучит смех, дарят друг другу радость и не жалуются на здоровье. Христос Воскрес!',
  'Поздравляю с Пасхой! Тебе желаю ясного солнца над головой, позитивных мыслей, добрых поступков, приветливых улыбок, безграничной любви и море блаженства.',
  'Поздравляю с весенним светлым христианским праздником Пасхи, желаем Вам очищения души, обновления и уюта в доме. Пусть Ваша семья всегда будет надёжным и тёплым кругом Вашей поддержки. Будьте любимы, здоровы и счастливы.',
  'Грянул светлый, добрый праздник — Пасха! Поздравляю с этим радостным днем. Желаю поистине душевного ликования, достатка, тепла, уюта, улыбок и семейного единства. Пусть Божья благодать осветит ваш дом и наполнит его миром, благополучием и душевностью!',
  'Поздравляю с Пасхой! В этот светлый, добрый, звонкий праздник — желаю всем море любви, семейного единения, духовного роста и ощутимого счастья. Будьте открыты вере и ее очищающей силе. Берегите родных и близких и живите в мире, полном благополучия, понимания и радушия!',
  'Христос воскрес! Пусть в этот светлый день ваша душа наполнится счастьем и умиротворением. Желаем, чтобы Господь охранял ваш дом, не впуская в него беду и горести. Великая Пасха настала, Спаситель наш Воистину воскрес!',
  'Поздравляю Вас со светлым пасхальным праздником, пусть в Вашей душе ощущается всегда присутствие Высшей силы, будут здоровы и успешны Ваши дети, крепки и счастливы Ваши семьи, активны и бодры Ваши родители. Пусть сопровождает Вас душевное спокойствие, материальное благополучие, уверенность в счастливом будущем и вера.',
