  'Поздравляю Вас с радостным и светлым весенним праздником обновления, с Пасхой! Пусть в этот святой день Ваше сердце будет открыто Богу, пусть придут в Вашу душу гармония и тепло. Желаем, чтобы Ваш дом наполнился уютом, звонким смехом, чтобы только добрые вести приходили сегодня в Вашу семью, будьте здоровы и благополучны.',
  'Поздравляю с праздником Пасхи, с радостным днём, когда над всеми нами раздалась благодать. Желаю тёплой и счастливой атмосферы в семье, достатка и понимания в доме, искренней надежды и щедрости в душе, чистой любви и доброго жизненного пути.',
  'Христос Воскрес! Пусть яркий свет этого великого праздника озарит сегодня весь мир, подарит надежду, радость и счастье людям. И пусть сегодня и всегда душа твоя искрится верой, помыслы — добром, а сердце — любовью.',
  'В светлый праздник Пасхи от души пожелать хочу большой радости и доброй надежды, благих намерений сердца и великой удачи, верного блага жизни и чистой любви, крепкого здравия и неугасаемого счастья.',
  'Пускай в сердце царит лишь свет; в доме — уют и благополучие; у близких — крепкое здоровье и радость в душе. Светлый праздник Пасха — день, когда, казалось бы, невозможное может стать действительностью, поэтому не переставайте верить. С праздником!',
  'В светлый праздник Пасхи, желаю всем людям на земле, быть искреннее к друг–другу и добрее. Пускай никогда не будет войн и катастроф, и все мы будем поистине счастливыми от того, что видим солнечный свет и цветение цветов, слышим пение птиц и смех детей. С святой Пасхой вас всех! Будьте здоровы и счастливы. Христос воскрес, воистину воскрес!',
  'Поздравляю тебя с Великой Пасхой! Светлым и радостным днем! Пусть Бог оберегает тебя от всяческих невзгод, дарит спокойствие и уют. Чтобы в семье царила гармония и понимание, а беды обходили стороной. Пусть святой праздник дарит добро тебе и окружающим тебя людям.',
  'Поздравляю со светлым праздником Пасхи! Пусть снизойдет на твой дом благодать, мир и покой, пусть царят в вашей семье любовь и доброта, забота друг о друге, здоровья и счастья Вам и Вашим близким, взаимопонимания, пусть ваша жизнь будет наполнена весельем и радостью.',
  'Великая Пасха — праздник всех православных. В столь праздничный день хочется пожелать добра здоровья, мира, процветания всем. Пусть каждый день ваш дом наполняется уютом, теплом, счастьем, благополучием. Дарите друг другу внимание и заботу, любовь и верность. С Праздником Светлой Пасхи!',
  'Поздравляю со светлой Пасхой! Желаю тебе много чудес и восхищений, огромного счастья в твоей семье и мирного светлого блага. Здоровья тебе, безграничной любви, неиссякаемого достатка и процветания.'
